.about_us-main {
    position: relative;
  }
  .bgc-ww_block-about_us {
    background-color: #E91E63;
    padding: 30px 0;
  }
  .ww_block-about_us {
    width: 80%;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
