  }
  .bg_container .video {
    width: 100%;
    position: relative;
    overflow: hidden;
  }
  .bg_container .video:hover .logo_container {
    top:20%;
  }
  .bg_container .video:hover video {
    -webkit-filter: blur(10px);
            filter: blur(10px);
  }
  .bg_container video {
    margin-top: -20px;
    z-index: -40;
    width: 100%;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
  }
  .bg_container .logo_container {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    top: 100%;
